@import url(https://fonts.googleapis.com/css2?family=Allura&family=Montserrat:wght@300;600&family=Rowdies:wght@700&family=Supermercado+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, rgba(7,27,82,1 )0%, rgba(0,128,128,1 )100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: 'SEGA LOGO FONT';
    src: url('http://fonts.cdnfonts.com/css/sega-logo-font');
        font-style: normal;
        font-weight: normal;
}
h1{
    font-family: 'Allura', cursive;
    font-family: 'Montserrat', sans-serif;
    font-family: 'Supermercado One', cursive;
    font-weight: 900;
    color: #0ccac4;
}
